import * as React from "react";
import "./style.scss";
import ProgressBar from "../../components/progressBar";
import { profileConfig } from "../../common";

interface Props {}

export default class SkillsPage extends React.Component<Props> {
  componentWillMount() {}

  renderSkills(skills: any) {
    const skillsValues = Object.values(skills);
    if (!skillsValues || skillsValues.length === 0) return null;
    return skillsValues.map((skill: any, idx) => {
      if (!skill || !skill.name || !skill.percent) return null;
      return (
        <div className="skill">
          <div className="name">{skill.name}</div>
          <ProgressBar percent={skill.percent} className="progress" />
        </div>
      );
    });
  }

  render() {
    const leftSideSkills = profileConfig("skills.leftSide");
    const rightSideSkills = profileConfig("skills.rightSide");
    return (
      <div className="page">
        <div className="page-container">
          <div className="skills">
            <div className="header">My Skills</div>
            <div className="skills-container">
              <div className="side">{this.renderSkills(leftSideSkills)}</div>
              <div className="side">{this.renderSkills(rightSideSkills)}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
