import React from "react";
import "./style.scss";
import { profileConfig } from "../../common";
import { resolve, relative } from "path";

interface IProps {}

interface IProject {
  name: string;
  imageName: string;
  details: string;
}

export default class ProjectsPage extends React.Component {
  constructor(props: IProps) {
    super(props);
    this.state = {};
  }

  render() {
    const myProjects: IProject[] = profileConfig("projects");
    return (
      <div className="page">
        <div className="header">My Projects</div>
        <div className="page-container">
          <div className="projects">
            {myProjects.map((project, idx) => {
              return (
                <div className="project" key={`${project.name}-${idx}`}>
                  <div className="container">
                    <div className="image">
                      <img
                        src={
                          project.imageName &&
                          require(`../../assets/images/${project.imageName}`)
                        }
                      />
                      <div className="details">{project.details}</div>
                    </div>
                  </div>
                  <div className="footer">{project.name}</div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}
