import React from "react";
import "./App.scss";
import Header from "./components/header";
import Router, { parseURLPath } from "./router";
import Particles from "react-particles-js";
//import particlesConfig from "./config/particlesjs.json";
import starsParticlesConfig from "./config/starsParticles.json";
import FogEffect from "./components/fog";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import Moon from "./components/moon";

interface IState {
  currentPath: string;
}
export default class App extends React.Component {
  state: IState;
  BAK_COLORS = [
    "radial-gradient(circle, #57539f 28%, #5858c2 70%, #2e94a8 100%)",
    "radial-gradient(circle, rgba(21,29,69,1) 4%, rgba(40,53,106,1) 50%, rgba(50,93,149,1) 100%)"
    //"radial-gradient(circle, rgba(180,70,69,1) 4%, rgba(199,63,63,1) 50%, rgba(246,121,98,1) 100%)"
  ];
  private appContainer: HTMLDivElement | null;

  constructor(props: any) {
    super(props);
    this.state = {
      currentPath: "home"
    };
  }

  componentWillMount() {
    //Set Page Title
    document.title = "Islem Penywis";
    const pathFromURL = parseURLPath();
    this.setState({ currentPath: pathFromURL });
  }

  componentDidMount() {
    this.changeBackgroundColor();
  }

  private onPathChange(pathname: string | null) {
    this.setState({ currentPath: pathname });
  }

  private changeBackgroundColor() {
    const rand = Math.floor(Math.random() * 2);
    const randColor = this.BAK_COLORS[1];
    (this.appContainer as HTMLDivElement).style.background = randColor;
  }

  render() {
    const { currentPath } = this.state;
    return (
      <TransitionGroup id="app" className="App">
        <div
          id="app"
          className="App"
          ref={appContainer => (this.appContainer = appContainer)}
        >
          <CSSTransition in={true} timeout={300} classNames="moon-wave">
            <Moon />
          </CSSTransition>
          <FogEffect />
          <Particles
            params={starsParticlesConfig as any}
            className="particles-container"
            canvasClassName="particles-canvas"
          />
          <Header
            onPathChange={this.onPathChange.bind(this)}
            currentPath={currentPath}
          />
          <CSSTransition
            in={true}
            key={currentPath}
            timeout={300}
            classNames="page-transition"
            mountOnEnter={false}
            unmountOnExit
          >
            <Router pathname={currentPath} />
          </CSSTransition>
        </div>
      </TransitionGroup>
    );
  }
}
