import React from "react";
import "./style.scss";

interface IProps {
  percent: number;
  className?: string;
}

export default class ProgressBar extends React.Component<IProps> {
  constructor(props: IProps) {
    super(props);
  }

  render() {
    const { percent, className } = this.props;
    let containerStyle;
    if (percent && percent >= 0 && percent <= 100)
      containerStyle = { width: `${percent}%` };
    else containerStyle = { width: 0 };
    return (
      <div className={`progress-bar ${className}`}>
        <div className="progress-bar-container">
          <div className="inner-progress" style={containerStyle} />
          <div
            className="percentage-text"
            style={{ color: percent >= 60 ? "#000" : "#fff" }}
          >
            {percent}%
          </div>
        </div>
      </div>
    );
  }
}
