import React from "react";
import "./style.scss";
import { profileConfig } from "../../common";

interface IError {
  [field: string]: string;
  message: string;
}

interface IState {
  email: string;
  subject: string;
  content: string;
  errors: IError[];
}

export default class ContactPage extends React.Component {
  readonly state: IState;
  emailRegex: RegExp;
  private contactForm: HTMLFormElement | null;

  constructor(props: any) {
    super(props);
    this.state = {
      email: "",
      subject: "",
      content: "",
      errors: []
    };
    this.emailRegex = /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/;
  }

  private setError(field: string, message: string) {
    this.setState((prevState: IState) => ({
      errors: [prevState.errors, { field, message }]
    }));
  }

  private getErrorMsg(field: string) {
    const { errors } = this.state;
    for (const err of errors) {
      if (err.field === field) return err.message;
    }
    return null;
  }

  private clearError(field: string) {
    this.setState((prevState: IState) => {
      let errors = [...this.state.errors];
      errors = errors.filter(err => {
        return err.field != field;
      });
      return { errors };
    });
  }

  private clearAllErrors() {
    this.setState({ errors: [] });
  }

  private onEmailInputChange(e: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ email: e.currentTarget.value });
  }

  private onSubjectInputChange(e: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ subject: encodeURIComponent(e.currentTarget.value) });
  }

  private onContentInputChange(e: React.ChangeEvent<HTMLTextAreaElement>) {
    this.setState({ content: encodeURIComponent(e.currentTarget.value) });
  }

  private onSubmit(e: React.FormEvent) {
    const { email, subject, content } = this.state;
    console.log("Email: ", email, "Subject", subject, "Content: ", content);
    //Validate
    if (!this.emailRegex.test(email))
      return this.setError("email", "Please Specify a Valid Email Address");
    if (!subject || subject === "")
      return this.setError("subject", "Please Specify a Subject");
    if (!content || content.trim() === "")
      return this.setError("content", "Please Specify Message Content");

    //Everything is good, submit form
    (this.contactForm as HTMLFormElement).submit();
  }

  render() {
    const myEmail = profileConfig("details.email");
    const { errors } = this.state;
    const emailErr = this.getErrorMsg("email");
    const subjectErr = this.getErrorMsg("subject");
    const contentErr = this.getErrorMsg("content");

    return (
      <div className="page">
        <div className="header">Contact Me</div>
        <div className="page-container">
          <div className="contact">
            <form
              className="form"
              action={`https://formspree.io/${myEmail}`}
              method="POST"
              ref={ref => (this.contactForm = ref)}
            >
              <div className="form-group">
                <label htmlFor="_replyto">Email</label>
                <input
                  type="email"
                  name="_replyto"
                  placeholder="Your Email"
                  onChange={this.onEmailInputChange.bind(this)}
                />
                {emailErr && <small className="form-error">{emailErr}</small>}
              </div>
              <div className="form-group">
                <label htmlFor="subject">Subject</label>
                <input
                  type="text"
                  name="subject"
                  placeholder="Subject"
                  onChange={this.onSubjectInputChange.bind(this)}
                />
                {subjectErr && (
                  <small className="form-error">{subjectErr}</small>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="content">Content</label>
                <textarea
                  name="content"
                  placeholder="Message Content"
                  onChange={this.onContentInputChange.bind(this)}
                />
                {contentErr && (
                  <small className="form-error">{contentErr}</small>
                )}
              </div>
              <div className="form-group">
                <button
                  type="button"
                  className="btn btn-submit"
                  onClick={this.onSubmit.bind(this)}
                >
                  Send Message
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}
