import * as React from "react";
import "./style.scss";

interface IProps {
  onPathChange: (pathname: string | null) => void;
  currentPath: string;
}

export default class Header extends React.Component<IProps> {
  private lineSelector: HTMLDivElement | null;

  constructor(props: IProps) {
    super(props);
    this.onPathClick = this.onPathClick.bind(this);
  }

  componentDidMount() {
    const { currentPath } = this.props;
    //find the right item to put the selector cursor on when component firstly mounts
    const navItems = document.getElementsByClassName("nav-item");
    Array.from(navItems).map(item => {
      let navItemHref = (item.firstElementChild as Element).getAttribute(
        "href"
      ) as string;
      if (!navItemHref) return;
      navItemHref = navItemHref.replace("#", "").toLowerCase();
      //Move cursor to initial value of current webpage
      if (currentPath.toLowerCase() === navItemHref) {
        this.setSelectorOnNavItem(item);
      }
    });
  }

  setSelectorOnNavItem(navItem: Element) {
    const itemPos = navItem.getBoundingClientRect();
    (this.lineSelector as HTMLDivElement).style.width = itemPos.width + "px";
    (this.lineSelector as HTMLDivElement).style.left = itemPos.left + "px";
  }

  onPathClick(e: React.MouseEvent) {
    //Move cursor to currently clicked nav item(path)
    this.setSelectorOnNavItem(e.currentTarget);
    this.props.onPathChange(e.currentTarget.innerHTML);
  }

  render() {
    return (
      <header>
        <div className="nav-bar">
          <div className="nav-bar-container">
            <ul>
              <li className="nav-item">
                <a href="#Home" onClick={this.onPathClick}>
                  Home
                </a>
              </li>
              <li className="nav-item">
                <a href="#Skills" onClick={this.onPathClick}>
                  Skills
                </a>
              </li>
              <li className="nav-item">
                <a href="#Projects" onClick={this.onPathClick}>
                  Projects
                </a>
              </li>
              <li className="nav-item">
                <a href="#Contact" onClick={this.onPathClick}>
                  Contact
                </a>
              </li>
            </ul>
            <div
              className="line-selector"
              ref={ref => (this.lineSelector = ref)}
            />
          </div>
        </div>
      </header>
    );
  }
}
