import * as React from "react";
import Thumbnail from "../../components/thumbnail";
import { profileConfig } from "../../common";
import Social from "../../components/social";
import { CSSTransition } from "react-transition-group";
import "./style.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

interface IProps {}

interface IState {
  showDetails: boolean;
}

export default class HomePage extends React.Component<IProps> {
  state: IState;
  thumbnail: HTMLDivElement;
  mainProfile: HTMLDivElement | null;

  constructor(props: IProps) {
    super(props);
    this.state = {
      showDetails: false
    };
  }

  showMoreDetailsSection() {
    //Move MainProfile container to the left
    (this.mainProfile as HTMLDivElement).style.transform = "translate(-10em)";
    this.setState({ showDetails: true });
  }

  onDetailsClose() {
    //Move MainProfile container back to the right
    if (this.mainProfile) {
      (this.mainProfile as HTMLDivElement).style.transform = null;
      this.setState({ showDetails: false });
    }
  }

  handleKeyPress(e: KeyboardEvent) {
    if (e.key === "Escape") this.onDetailsClose();
  }

  componentDidMount() {
    document.addEventListener("keydown", this.handleKeyPress.bind(this), false);
  }

  componentWillMount() {
    document.removeEventListener(
      "keydown",
      this.handleKeyPress.bind(this),
      false
    );
  }

  render() {
    const { showDetails } = this.state;
    return (
      <div className="page">
        <div className="page-container">
          <div className="main-profile" ref={ref => (this.mainProfile = ref)}>
            <Thumbnail
              onMoreDetailsClick={this.showMoreDetailsSection.bind(this)}
              containerRef={ref => (this.thumbnail = ref)}
              isDetailsOpen={showDetails}
            />
            <div className="my-title">{profileConfig("title")}</div>
            <Social />
          </div>
          <CSSTransition
            in={showDetails}
            timeout={1000}
            classNames="details-anim"
            unmountOnExit
          >
            <Details onClose={this.onDetailsClose.bind(this)} />
          </CSSTransition>
        </div>
      </div>
    );
  }
}

interface IDetailsProps {
  onClose: () => void;
}

const Details: React.FC<IDetailsProps> = (props: IDetailsProps) => {
  return (
    <div className="more-details">
      <div className="details-header">
        <div onClick={props.onClose}>
          <FontAwesomeIcon icon={faTimes} className="close-icon" size="1x" />
        </div>
      </div>
      <div className="container">
        <div className="item">{profileConfig("details.title")}</div>
        <div className="item">{profileConfig("details.tutorials")}</div>
        <div className="item">
          Different Techonologies{" "}
          <div className="highlight">
            {profileConfig("details.technologies")}
          </div>
        </div>
        <div className="item">
          Contact Email:{" "}
          <a href={`mailto:${profileConfig("details.email")}`}>
            <div className="highlight">{profileConfig("details.email")}</div>
          </a>
        </div>
      </div>
    </div>
  );
};
