import * as React from "react";
import HomePage from "./pages/home";
import SkillsPage from "./pages/skills";
import { CSSTransition } from "react-transition-group";
import ProjectsPage from "./pages/projects";
import ContactPage from "./pages/contact";

const DEFAULT_PATH = "home";

export const parseURLPath = () => {
  //Get current Location hash (ex: #Home)
  return window.location.hash.replace("#", "").toLowerCase() || DEFAULT_PATH;
};

interface IProps {
  pathname: string;
}

const Router: React.FC<IProps> = (props: IProps) => {
  const { pathname } = props;
  //Current to render page (Home by default)
  let currentPage = <HomePage />;
  switch (pathname.toLowerCase()) {
    case "home":
    case "":
      currentPage = <HomePage />;
      break;
    case "skills":
    case "skill":
      currentPage = <SkillsPage />;
      break;
    case "projects":
    case "project":
      currentPage = <ProjectsPage />;
      break;
    case "Contact":
    case "contact":
    case "contactUs":
    case "contactus":
      currentPage = <ContactPage />;
      break;
    //TODO: Add other Pages
  }

  return currentPage;
};

export default Router;
