import React from "react";
import profileImage from "../../assets/images/profile_img.jpg";
import { profileConfig } from "../../common";
import "./style.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";

interface IProps {
  onMoreDetailsClick: () => void;
  containerRef?: (ref: HTMLDivElement) => void;
  isDetailsOpen?: boolean;
}

export default class Thumbnail extends React.Component<IProps> {
  private thumbnail: HTMLDivElement;
  private myTitle: HTMLDivElement;
  private social: HTMLDivElement;

  static defaultProps = {
    isDetailsOpen: false
  };

  constructor(props: IProps) {
    super(props);
  }

  onDetailsClick() {
    if (this.props.onMoreDetailsClick) this.props.onMoreDetailsClick();
  }

  render() {
    const { containerRef, isDetailsOpen } = this.props;
    return (
      <div className="my-thumbnail" ref={containerRef}>
        <div className="my-thumbnail-container">
          <div
            className={`image ${!isDetailsOpen ? "show" : ""}`}
            onClick={this.onDetailsClick.bind(this)}
          >
            <img
              src={
                "https://images.pexels.com/photos/617278/pexels-photo-617278.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"
              }
            />
            <div className="details">
              More Details <FontAwesomeIcon icon={faArrowRight} size="sm" />
            </div>
          </div>
          <div className="name">
            <b>{profileConfig("fullname")}</b>
          </div>
          <div className="aka">
            <b>AKA</b>
            {profileConfig("aka")}
          </div>
          <div className="email">
            <b>Email</b>
            {profileConfig("details.email")}
          </div>
        </div>
      </div>
    );
  }
}
